import React, { useState, useEffect, useRef } from 'react';
import Z_INDEX from '../../constants/zIndex';

interface SearchPanelProps {
  isVisible: boolean;
  onClose: () => void;
  onSearch: (query: string, options: SearchOptions) => void;
  onReplace: (searchQuery: string, replaceText: string, options: SearchOptions) => void;
  onReplaceAll: (searchQuery: string, replaceText: string, options: SearchOptions) => void;
  onFindNext: () => void;
  onFindPrevious: () => void;
  searchResults: SearchResult[];
  currentResultIndex: number;
  theme: 'light' | 'dark';
}

interface SearchOptions {
  caseSensitive: boolean;
  useRegex: boolean;
  wholeWord: boolean;
}

interface SearchResult {
  line: number;
  column: number;
  length: number;
  text: string;
}

/**
 * è‡ªå®šä¹‰æœç´¢é¢æ¿ï¼Œå›ºå®šåœ¨èœå•è¡Œå³ä¾§
 */
const SearchPanel: React.FC<SearchPanelProps> = ({
  isVisible,
  onSearch,
  onReplace,
  onReplaceAll,
  onFindNext,
  onFindPrevious,
  theme
}) => {
  const [searchQuery, setSearchQuery] = useState('');
  const [replaceText, setReplaceText] = useState('');
  const [showReplace, setShowReplace] = useState(false);
  const [options, setOptions] = useState<SearchOptions>({
    caseSensitive: false,
    useRegex: false,
    wholeWord: false
  });
  
  const searchInputRef = useRef<HTMLInputElement>(null);
  
  // å½“é¢æ¿å˜ä¸ºå¯è§æ—¶ï¼Œèšç„¦æœç´¢è¾“å…¥æ¡†
  useEffect(() => {
    if (isVisible && searchInputRef.current) {
      searchInputRef.current.focus();
    }
  }, [isVisible]);
  
  // å¤„ç†æœç´¢ - å½“è¾“å…¥æ¡†å†…å®¹å˜åŒ–æ—¶è‡ªåŠ¨è§¦å‘æœç´¢
  useEffect(() => {
    if (searchQuery.trim()) {
      onSearch(searchQuery, options);
    }
  }, [searchQuery, options, onSearch]);
  
  // å¤„ç†æ›¿æ¢
  const handleReplace = () => {
    if (searchQuery.trim()) {
      onReplace(searchQuery, replaceText, options);
    }
  };
  
  // å¤„ç†å…¨éƒ¨æ›¿æ¢
  const handleReplaceAll = () => {
    if (searchQuery.trim()) {
      onReplaceAll(searchQuery, replaceText, options);
    }
  };
  
  // å¤„ç†æŒ‰é”®äº‹ä»¶
  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      if (e.shiftKey) {
        onFindPrevious();
      } else {
        onFindNext();
      }
      e.preventDefault();
    }
  };
  
  // åˆ‡æ¢é€‰é¡¹
  const toggleOption = (option: keyof SearchOptions) => {
    setOptions(prev => ({
      ...prev,
      [option]: !prev[option]
    }));
  };

  const inputClass = `
    w-full px-2 py-1 text-xs border rounded-md focus:outline-none focus:ring-1 focus:ring-blue-500
    ${theme === 'dark' 
      ? 'bg-gray-800 text-gray-200 border-gray-600' 
      : 'bg-white text-gray-700 border-gray-300'
    }
  `;
  
  const buttonClass = `
    px-1.5 py-0.5 text-xs font-medium rounded-md transition-colors
    ${theme === 'dark' 
      ? 'bg-gray-700 hover:bg-gray-600 text-gray-200 border-gray-600' 
      : 'bg-gray-100 hover:bg-gray-200 text-gray-700 border-gray-300'
    }
    border
  `;
  
  const optionButtonClass = (active: boolean) => `
    px-1.5 py-0.5 text-xs font-medium rounded-md transition-colors border
    ${theme === 'dark'
      ? active 
        ? 'bg-blue-600 text-white border-blue-700' 
        : 'bg-gray-700 text-gray-300 border-gray-600 hover:bg-gray-600'
      : active
        ? 'bg-blue-500 text-white border-blue-600'
        : 'bg-gray-100 text-gray-700 border-gray-300 hover:bg-gray-200'
    }
  `;

  return (
    <div 
      className={`flex-shrink-0 ${!isVisible ? 'hidden' : ''}`}
      style={{ 
        backgroundColor: theme === 'dark' ? '#1f2937' : 'white',
        borderLeft: `1px solid ${theme === 'dark' ? '#374151' : '#e5e7eb'}`,
        color: theme === 'dark' ? '#f3f4f6' : '#111827',
        padding: '0.25rem 0.5rem',
        zIndex: Z_INDEX.SEARCH_PANEL,
        height: '100%',
        display: 'flex',
        alignItems: 'center',
        position: 'absolute',
        right: 0,
        top: 0
      }}
    >
      <div className="flex items-center space-x-1">
        {/* æœç´¢è¾“å…¥æ¡† */}
        <input
          ref={searchInputRef}
          type="text"
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          onKeyDown={handleKeyDown}
          placeholder="æœç´¢..."
          className={inputClass}
          style={{ width: '150px' }}
        />
        
        {/* æœç´¢é€‰é¡¹æŒ‰é’® */}
        <button 
          className={optionButtonClass(options.caseSensitive)}
          onClick={() => toggleOption('caseSensitive')}
          title="åŒºåˆ†å¤§å°å†™"
        >
          Aa
        </button>
        <button 
          className={optionButtonClass(options.wholeWord)}
          onClick={() => toggleOption('wholeWord')}
          title="å…¨å­—åŒ¹é…"
        >
          W
        </button>
        <button 
          className={optionButtonClass(options.useRegex)}
          onClick={() => toggleOption('useRegex')}
          title="ä½¿ç”¨æ­£åˆ™è¡¨è¾¾å¼"
        >
          .*
        </button>
        
        {/* å¯¼èˆªæŒ‰é’® */}
        <button 
          className={buttonClass}
          onClick={onFindPrevious}
          title="æŸ¥æ‰¾ä¸Šä¸€ä¸ª (Shift+Enter)"
        >
          â†‘
        </button>
        <button 
          className={buttonClass}
          onClick={onFindNext}
          title="æŸ¥æ‰¾ä¸‹ä¸€ä¸ª (Enter)"
        >
          â†“
        </button>
        
        {/* æ›¿æ¢åˆ‡æ¢æŒ‰é’® */}
        <button 
          className={buttonClass}
          onClick={() => setShowReplace(!showReplace)}
          title={showReplace ? "éšè—æ›¿æ¢" : "æ˜¾ç¤ºæ›¿æ¢"}
        >
          {showReplace ? "âˆ’" : "+"}
        </button>
        
        {/* æ›¿æ¢è¾“å…¥æ¡†å’ŒæŒ‰é’® */}
        {showReplace && (
          <>
            <input
              type="text"
              value={replaceText}
              onChange={(e) => setReplaceText(e.target.value)}
              placeholder="æ›¿æ¢ä¸º..."
              className={inputClass}
              style={{ width: '120px' }}
            />
            <button 
              className={buttonClass}
              onClick={handleReplace}
              title="æ›¿æ¢"
            >
              æ›¿æ¢
            </button>
            <button 
              className={buttonClass}
              onClick={handleReplaceAll}
              title="å…¨éƒ¨æ›¿æ¢"
            >
              å…¨éƒ¨
            </button>
          </>
        )}
      </div>
    </div>
  );
};

export default SearchPanel;